import React from 'react';

interface Url {
  id: string;
  originalUrl: string;
  shortCode: string;
  clickCount: number;
}

interface UrlListProps {
  urls: Url[];
  onDelete: (id: string) => void;
}

const UrlList: React.FC<UrlListProps> = ({ urls, onDelete }) => {
  return (
    <div>
      <h2>Shortened URLs</h2>
      <table>
        <thead>
          <tr>
            <th>Original URL</th>
            <th>Short Code</th>
            <th>Click Count</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {urls.map((url) => (
            <tr key={url.id}>
              <td>{url.originalUrl}</td>
              <td>{url.shortCode}</td>
              <td>{url.clickCount}</td>
              <td>
                <button onClick={() => onDelete(url.id)}>Delete</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default UrlList;